.weather-now {
    display: grid;
    row-gap: 15px;

    .temp-description {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 25px;
        flex-wrap: wrap;

        .temperature {
            font-size: 48px;
        }

        img {
            width: 180px;
        }

        .description {
            padding: 15px;
            background-color: #fff;
            backdrop-filter: blur(10px);

            p:first-child {
                font-size: 24px;
            }

            p:last-child {
                font-size: 16px;
            }
        }
    }

    .additions {
        ul {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 25px;
            li{
                display: flex;
                flex-direction: column;
            }
        }
        @media(max-width: 425px){
            ul{
                flex-direction: column;
                align-items: start;
                li{
                    flex-direction: row;
                }
            }
        }
    }
}