*{
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 600;
}

.container {
  min-width: min-content;
  max-width: 1024px;
  margin: 0 auto;
  padding: 25px;
  border-radius: 15px;
  background-color: #e0e0e0;

  .city-time {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    .city {
      display: flex;
      flex-direction: column;
      position: relative;
      .current-city {}

      .city-input {
        // display: none;
      }
      .city-output-container{
        position: relative;
      }
      .city-output {
        position: absolute;
        backdrop-filter: blur(15px);
        background-color: rgba($color: #fdfdfd33, $alpha: 0.3);
      }
    }

    .current-date {}

  }
}