.weather-future{
    font-size: 12px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5px;
}
.weather-future-card{
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    padding: 5px;
}
.weather-future-temp-img{
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.weather-future-description{
    display: flex;
    flex-direction: column;
}