.weather-now {
  display: grid;
  row-gap: 15px;
}
.weather-now .temp-description {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  flex-wrap: wrap;
}
.weather-now .temp-description .temperature {
  font-size: 48px;
}
.weather-now .temp-description img {
  width: 180px;
}
.weather-now .temp-description .description {
  padding: 15px;
  background-color: #fff;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.weather-now .temp-description .description p:first-child {
  font-size: 24px;
}
.weather-now .temp-description .description p:last-child {
  font-size: 16px;
}
.weather-now .additions ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
}
.weather-now .additions ul li {
  display: flex;
  flex-direction: column;
}
@media (max-width: 425px) {
  .weather-now .additions ul {
    flex-direction: column;
    align-items: start;
  }
  .weather-now .additions ul li {
    flex-direction: row;
  }
}/*# sourceMappingURL=weatherNow.css.map */